// store/index.js
import { createStore } from "vuex";
import axios from "axios";

const apiBaseUrl = "http://localhost/cafe-ps8/api/orders.php";

export default createStore({
  state: {
    Places: [
      { id: "Room1", Room: " 1", items: [], Total: 0 },
      { id: "Room2", Room: " 2", items: [], Total: 0 },
      { id: "Room3", Room: " 3", items: [], Total: 0 },
      { id: "Room4", Room: " 4", items: [], Total: 0 },
      { id: "Room5", Room: " 5", items: [], Total: 0 },
      { id: "Room6", Room: " 6", items: [], Total: 0 },
      { id: "Room7", Room: " 7", items: [], Total: 0 },
      { id: "Room8", Room: " 8", items: [], Total: 0 },
      { id: "PS1", Room: "PS1", items: [], Total: 0 },
      { id: "PS2", Room: "PS2", items: [], Total: 0 },
      { id: "PS3", Room: "PS3", items: [], Total: 0 },
      { id: "PS4", Room: "PS4", items: [], Total: 0 },
      { id: "PS5", Room: "PS5", items: [], Total: 0 },
      { id: "PS6", Room: "PS6", items: [], Total: 0 },
      { id: "PS7", Room: "PS7", items: [], Total: 0 },
      { id: "PS8", Room: "PS8", items: [], Total: 0 },
      { id: "Delivery", Room: "Delivery", items: [], Total: 0 },
      { id: "TakeAway", Room: "TakeAway", items: [], Total: 0 },
    ],
    NewItem: "",
    NewQuantity: "",
    NewPlace: "",
    Products: [],
    OPlace: "",
    NPlace: "",
    PSs: [],
    PSW: ["", "", "", "", "", "", "", ""],
    PST: [0, 0, 0, 0, 0, 0, 0, 0],
    PSWT: "",
    PSTT: "",
    time: "",
  },
  mutations: {
    SET_PLACES(state, places) {
      state.Places = places;
    },
    SET_PRODUCTS(state, products) {
      state.Products = products;
    },
    SET_PS(state, ps) {
      state.PSs = ps;
    },
    SET_PSW(state, psw) {
      state.PSW = psw;
    },
    SET_PST(state, pst) {
      state.PST = pst;
    },
    SET_TIME(state, time) {
      state.time = time;
    },
  },
  actions: {
    async globalFunction({ dispatch }) {
      setInterval(() => {
        dispatch("getTime");
        for (let i = 0; i < 8; i++) {
          if (this.state.PST[i] !== 0 && this.state.PSs[i].state === 1) {
            const Start = new Date(this.state.PSs[i].start.replace(" ", "T"));
            const End = new Date(this.state.time.replace(" ", "T"));
            const hours = ((End - Start) / (1000 * 60 * 60)).toFixed(2);
            console.log(hours);
            if (this.state.PST[i] - hours <= 0) {
              dispatch("StopPS", i);
            }
          }
        }
      }, 30000);
    },
    async getTime({ commit }) {
      const now = new Date();
      const formattedTime = now.toISOString().slice(0, 19).replace("T", " ");
      commit("SET_TIME", formattedTime);
    },
    async StopPS({ dispatch, state }, index) {
      await dispatch("getTime");
      try {
        const response = await axios.put(apiBaseUrl, {
          IdPS: index + 1,
          stop: 0,
        });
        console.log(response.data.msg);
      } catch (error) {
        console.error("fail:", error);
      }

      let price;
      switch (state.PSW[index]) {
        case "normal":
          price = state.PSs[index].normal_price;
          break;
        case "multi":
          price = state.PSs[index].multi_price;
          break;
        case "plus":
          price = state.PSs[index].plus_price;
          break;
        case "netflix":
          price = state.PSs[index].netflix_price;
          break;
        default:
          price = 0;
      }

      const Start = new Date(state.PSs[index].start.replace(" ", "T"));
      const End = new Date(state.time.replace(" ", "T"));
      const hours = ((End - Start) / (1000 * 60 * 60)).toFixed(2);
      state.PST[index] = hours;

      state.Places[index + 8].items.unshift({
        Order: state.PSW[index],
        Quantity: state.PST[index],
        Price: price,
      });

      let total = 0;
      state.Places[index + 8].items.forEach((item) => {
        total += item.Quantity * item.Price;
      });
      state.Places[index + 8].Total = total.toFixed(2);

      await dispatch("UpdateItems");
      dispatch("GetProducts");
    },
    async UpdateItems({ state }) {
      try {
        const response = await axios.put(apiBaseUrl, {
          JSON: state.Places,
          JSONW: state.PSW,
          JSONT: state.PST,
        });
        console.log(response.data.msg);
      } catch (error) {
        console.error("fail:", error);
      }
    },
    async GetProducts({ commit }) {
      try {
        const response = await axios.get(apiBaseUrl);
        commit("SET_PRODUCTS", response.data.Products);
        commit("SET_PLACES", JSON.parse(response.data.Places[0].orders));
        commit("SET_PSW", JSON.parse(response.data.Places[1].orders));
        commit("SET_PST", JSON.parse(response.data.Places[2].orders));
        commit("SET_PS", response.data.PSs);
        console.log(response.data.msg);
      } catch (error) {
        console.error("fail:", error);
      }
    },
  },
  getters: {
    allPlaces(state) {
      return state.Places;
    },
    allProducts(state) {
      return state.Products;
    },
    allPSs(state) {
      return state.PSs;
    },
    getTime(state) {
      return state.time;
    },
  },
});
